@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: local("PT Sans"), local("PT Sans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: local("PT Sans Bold"), local("PT Sans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.break {
  display: none;
}
@media (min-width: 768px) {
  .break {
    display: inline;
  }
}
.break-desktop {
  display: none;
}
@media (min-width: 1200px) {
  .break-desktop {
    display: inline;
  }
}
@media (min-width: 1200px) {
  .break-clear {
    display: none;
  }
}
.button {
  display: block;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  width: auto;
  box-sizing: border-box;
  background-color: #81B3D2;
  color: #FFFFFF;
  padding: 16px 32px;
  text-align: center;
  cursor: pointer;
}
.button:hover {
  background-color: #669EC0;
}
.button:active {
  color: rgba(255, 255, 255, 0.2);
  background-color: #5496BD;
}
.button:disabled {
  background-color: #E5E5E5;
}
.page {
  height: 100%;
}
.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
  color: #333333;
  margin: 0;
  padding: 0;
  background-color: #E7E5E5;
}
.page__wrapper {
  background-color: #FFFFFF;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  max-width: 1200px;
  margin: 0 auto;
}
.page-header {
  position: relative;
  padding-bottom: 56px;
}
@media (min-width: 768px) {
  .page-header {
    padding-bottom: 0;
  }
}
.page-header__logo {
  position: absolute;
  bottom: -35px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}
@media (min-width: 768px) {
  .page-header__logo {
    top: 0;
  }
}
@media (min-width: 1200px) {
  .page-header__image {
    width: 187px;
    height: 96px;
  }
}
.main-nav {
  position: relative;
}
.main-nav::after {
  content: "";
  position: absolute;
  bottom: 1px;
  width: 100%;
  height: 7px;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav::after {
    content: none;
  }
}
.main-nav__button {
  position: absolute;
  bottom: -56px;
  right: 0;
  width: 56px;
  height: 56px;
  background-color: transparent;
  border: none;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav__button {
    display: none;
  }
}
.main-nav__button::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 13px;
  width: 23px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 8px 0 0 #81B3D2, 0 16px 0 0 #81B3D2;
}
.main-nav__button:hover::before {
  opacity: 0.6;
}
.main-nav__button:active::before {
  opacity: 0.3;
}
.main-nav__button--close {
  top: 0;
  background-image: url("../img/icons/close.svg");
  background-repeat: no-repeat;
  background-size: 22px 22px;
  background-position: center;
}
.main-nav__button--close::before {
  content: none;
}
.main-nav__button--close:hover {
  opacity: 0.6;
}
.main-nav__button--close:active {
  opacity: 0.3;
}
.main-nav__button--none {
  display: none;
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: none;
}
.main-nav__list--current {
  display: grid;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
  .main-nav__list::before {
    content: "";
    display: block;
    width: 1px;
    height: 1px;
    order: 1;
  }
}
.main-nav__item:nth-child(3),
.main-nav__item:nth-child(4) {
  order: 2;
}
.main-nav__item a {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  color: #FFFFFF;
  text-align: center;
  padding: 8px 0 7px;
  background-color: #81B3D2;
  box-shadow: 0 1px 0 rgba(0, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid #5496BD;
}
.main-nav__item a:hover {
  background-color: #669EC0;
}
.main-nav__item a:active {
  color: rgba(255, 255, 255, 0.2);
  background-color: #5496BD;
}
@media (min-width: 768px) {
  .main-nav__item a {
    font-size: 14px;
    color: #000000;
    background-color: transparent;
    box-shadow: none;
    border-bottom: none;
  }
  .main-nav__item a:hover {
    color: #81B3D2;
    background-color: transparent;
  }
  .main-nav__item a:active {
    color: rgba(0, 1, 1, 0.3);
    background-color: transparent;
  }
}
@media (min-width: 768px) {
  .main-nav__item--current a {
    color: #766357;
  }
  .main-nav__item--current a:hover {
    color: #604E43;
  }
  .main-nav__item--current a:active {
    color: #766357;
    opacity: 0.3;
  }
}
