.page-header {
  position: relative;
  padding-bottom: 56px;

  @media (min-width: @tablet-width) {
    padding-bottom: 0;
  }
}

.page-header__logo {
  position: absolute;
  bottom: -35px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;

  @media (min-width: @tablet-width) {
    top: 0;
  }
}

.page-header__image {
  @media (min-width: @desktop-width) {
    width: 187px;
    height: 96px;
  }
}
